<template lang="pug">

.main
	.item
		.item-title Персональные данные
		.form
			label Имя
			input(v-model="profile.name")
		.form
			label Фамилия
			input(v-model="profile.surname")
	.item
		.item-title Контактные данные
		.form
			label Телефонный номер
			input(v-model="profile.tel")
		.form
			label Почта
			input(v-model="profile.email")
	.item
		.item-title Адреса
		.form.form-copy(v-for="(address, i) in profile.address")
			label Адрес
			input(v-model="profile.address[i]")
			.form-copy-minus -
		.form.form-copy
			label Адрес
			input(v-model="newAddress" placeholder="Новый адрес")
			.form-copy-plus +
	.item.save(@click="save") Сохранить

</template>

<script>

export default {
	data(){
		return {
			newAddress: ''
		}
	},
	methods: {
		save(){
			this.$store.dispatch('profile/updateProfile', this.profile)
		}
	},
	computed: {
		profile:{
			get(){
				let profile = this.$store.state.profile.profile
				return {
					name: profile.name,
					surname: profile.surname,
					tel: profile.tel,
					email: profile.email,
					address: profile.address
				}
			}
		}
	}
}

</script>

<style lang="stylus" scoped>


.item
	margin-bottom 20px
	width 345px
	background-color #fff
	padding 15px 20px 20px
	box-shadow 0 0 6px 0 rgba(#000, .1)
	border-radius: 15px
	margin-right 22px
	&:nth-child(3n)
		margin-right 0
	&-title
		font-size 16px
		font-weight 600
		text-align center
		margin-bottom 15px

.save
	background-color rgba(#00e600, .6)
	font-size 25px
	font-weight 700
	min-height 120px
	color #ffffff
	display flex
	align-items center
	justify-content center
	align-self stretch
	transition background-color .3s
	cursor pointer
	user-select none
	&:hover
		background-color rgba(#00e600, .8)
	&:active
		background-color rgba(#00e600, .5)

.form
	margin-top 10px
	&:first-child
		margin-top 0
	label
		font-size 16px
		margin-bottom 5px
		display block
	input
		display block
		width 100%
		height 28px
		font-size 14px
		background-color #f6f6f6
		border 0
		border-radius 6px
		padding 5px 13px
		&::placeholder
			color #888
	&-copy
		input
			width calc(100% - 40px)
			display inline-block
		&-plus, &-minus
			display inline-block
			float right
			font-size 21px
			line-height 28px
			text-align center
			height 28px
			width 28px
			border-radius 6px
			background-color #f6f6f6
			color #999
			font-weight 600
			cursor pointer
			transition background-color .2s, color .2s
			&:hover
				background-color #e8e8e8
				color #888
			&:active
				background-color #aaa
				color #eee

</style>